obtenerproedio( copia:any){
  this.promedio = null;
  let promedio: number = 0; // variable par obtener el promedio
  for (let i in copia) { 
    promedio += copia[i].calificacion;
  }
  promedio = promedio/copia.length;
  this.promedio = promedio
}

obtenerMejor(copia:any){
  console.log(copia)
  let chingon: any;
  let promedio = 0;
  let mejorp = 0;
  let enc = 0;
  let entro = 0;
  let mejorm: any;
  let respaldo: any;
  chingon = copia[0].nombre;
  for (let i  of copia) { 
  let matricula = i.nombre;
  if(matricula == chingon){
      promedio += i.calificacion;
      chingon = matricula;
      enc += 1;
  }else if(matricula != chingon){
    entro += 1;
    if(promedio > mejorp){
      mejorm = chingon;
      mejorp = promedio
      if(entro == 1){
        respaldo = enc; 
      }     
      enc=1}
      promedio = i.calificacion;
      chingon = matricula;
  } 
  }
  if(entro == 0){
  mejorm = chingon;
  mejorp = promedio;
  }
  console.log(respaldo)
  this.promedio = mejorp/respaldo 
  this.pasaron = "MEJOR ESTUDIANTE: "+ mejorm;
}

separodeDumis(copia: any[], mensajev: any, mensajer: any){
  this.pasaron = null;
  this.reprobados = null;
  let menzos: number = 0;
  let dostres: number =0;
  
  for (let i in copia) { 
    let enc: number = 0;
    if(copia[i].calificacion>= 6 ){
      dostres += 1;
    }else{
      menzos += 1;
      if ( this.alumnosrep.length == 0){
        console.log("se inserto uno")
        this.alumnosrep.push(copia[i].nombre);
        
      }else{
      for(let j in this.alumnosrep){
        console.log("PENDEJO"+ this.alumnosrep[j])
        console.log("COMPIA "+copia[i].nombre)
        if(this.alumnosrep[j] == copia[i].nombre){
          enc = 1;
          break;
        }
      } 
      if (enc != 1) {
        this.alumnosrep.push(copia[i].nombre);
      }
    }
    }
  } 
  this.pasaron = mensajev+ " "+ dostres;
  this.reprobados = mensajer+ " "+ menzos;
  console.log(this.alumnosrep)
}

